<template>
  <div>
    <generic-grid
      :params="params"
      :service="service"
      :headers="gridHeaders"
      :getDataCb="getDataCb"
      :toolbarTitle="toolbarTitle"
      :storeMutationKey="storeMutationKey"
    >
      <template v-slot:createForm="{ onSave, onClose, isOpen, isSaving }">
        <producers-form
          v-if="isOpen"
          :stream="stream"
          :isEdit="false"
          :onSave="onSave"
          :onCancel="onClose"
          :isSaving="isSaving"
        ></producers-form>
      </template>
      <template v-slot:editForm="{ onSave, item, onClose, isOpen, isSaving }">
        <producers-form
          v-if="isOpen"
          :item="item"
          :isEdit="true"
          :onSave="onSave"
          :onCancel="onClose"
          :isSaving="isSaving"
        ></producers-form>
      </template>
    </generic-grid>
  </div>
</template>
<script>
import ProducersForm from './producersForm.vue';
import GenericGrid from '../../Common/GenericGrid.vue';
import { rfProducersService } from '../../../services/rfProducersService';

export default {
  components: { GenericGrid, ProducersForm },
  name: 'producersGrid',
  props: ['params', 'getDataCb'],
  data() {
    return {
      service: rfProducersService,
      storeMutationKey: 'changeRfProducers',
      toolbarTitle: 'Producers',
      gridHeaders: [
        {
          text: 'Name',
          align: 'center',
          value: 'name',
          sortable: true,
        },
        {
          text: 'Actions',
          align: 'center',
          value: 'actions',
          sortable: true,
        },
      ],
    };
  },
  computed: {
    stream() {
      return {
        uuid: this.params.uuid,
      };
    },
  },
};
</script>
