<template>
  <v-card>
    <v-card-title>
      <v-btn @click="onBack" icon medium class="mr-1">
        <v-icon medium> mdi-arrow-left </v-icon>
      </v-btn>
      Producers
    </v-card-title>

    <v-card-text>
      <breadcrumbs :items="breadcrumbItems"></breadcrumbs>
      <v-row no-gutter>
        <v-col cols="12" lg="12" xl="12" md="12" sm="12" xs="12">
          <producers-grid v-if="params.uuid" :params="params" :getDataCb="getDataCb"> </producers-grid>
        </v-col>
      </v-row>
    </v-card-text>
  </v-card>
</template>
<script>
import ProducersGrid from './producersGrid.vue';
import TooltipIconButton from '../../RubixApps/TooltipIconButton.vue';
import IconButton from '../../Common/IconButton.vue';
import Breadcrumbs from '../../Common/Breadcrumbs.vue';
import _get from 'lodash/get';
import routeConstant from '../../../constants/routes';

const baseHref = routeConstant.rfStreamsRoot;


export default {
  name: 'producers',
  data() {
    return {
      params: {
        uuid: null,
        producers: true,
      },
      gridData: {},
      breadcrumbItems: [
        {
          text: 'Streams',
          disabled: false,
          link: baseHref,
        },
      ],
    };
  },
  components: {
    IconButton,
    ProducersGrid,
    TooltipIconButton,
    Breadcrumbs,
  },
  mounted() {
    this.params = { ...this.params, uuid: this.$route.params.streamId };
  },
  methods: {
    onBack() {
      this.$router.go(-1);
    },
    getDataCb(data) {
      this.gridData = data;
      this.updateBreadCrumbs();
    },
    updateBreadCrumbs() {
      const streamName = _get(this.gridData, 'extraData.name', '');
      if (streamName) {
        this.breadcrumbItems.push({ text: streamName, align: 'center', disabled: true });
      }
    },
  },
};
</script>
